@font-face {
  font-family: 'MFNianzhen';
  font-weight: normal;
  src: url('./assets/fonts/MFNianZhen-Noncommercial-Regular.woff');
}
@font-face {
  font-family: 'MFKeSong';
  font-weight: normal;
  src: url('./assets/fonts/MFKeSong_Noncommercial-Regular.woff');
}
* {
  -webkit-transition: all ease 0.4s;
  -o-transition: all ease 0.4s;
  transition: all ease 0.4s;
  font-family: 'MFNianzhen';
}
div {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  background-color: #f0f5e5;
  margin: 0;
  padding: 0;
}
.content-wrapper {
  max-width: 80rem;
  padding: 1rem 2.5rem;
  left: 0;
  right: 0;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.content-wrapper .main-content {
  -ms-flex: 0.625 1;
      flex: 0.625 1;
}
.content-wrapper .rail-content {
  -ms-flex: 0.33 1;
      flex: 0.33 1;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
p,
div,
span {
  font-size: 1.3rem;
}
h1 {
  font-size: 2.5rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.8rem;
}
h4 {
  font-size: 1.5rem;
}
h5 {
  font-size: 1.3rem;
}
h1,
h2,
h3,
h4,
h5 {
  color: #A5C563;
  font-weight: normal;
  margin-top: 0.5rem;
}
a {
  color: #A5C563;
  text-decoration: none;
}
a:hover {
  color: #bad388;
}
hr {
  background-color: #8a8a8a;
  width: 100%;
  height: 1px;
  content: ' ';
  border: none;
}
.fade-in {
  opacity: 0;
}
.fade-in.animating {
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
  opacity: 1;
}
.fade-out {
  opacity: 1;
}
.fade-out.animating {
  -webkit-transition: opacity 500ms ease-out;
  -o-transition: opacity 500ms ease-out;
  transition: opacity 500ms ease-out;
  opacity: 0;
}
.article img {
  width: 100%;
}



