header {
  height: 4rem;
  background-color: white;
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.16);
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.16);
}
.nav-wrapper {
  max-width: 80rem;
  padding: 0 2.5rem;
  left: 0;
  right: 0;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}
.nav-wrapper nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
}
.nav-wrapper nav a {
  color: #A5C563;
  text-decoration: none;
  outline: none;
  font-size: 20px;
  text-align: center;
  margin-right: 30px;
  line-height: 4rem;
  position: relative;
}
.nav-wrapper nav a span {
  display: inline-block;
  padding: 0 1.5rem;
}
.nav-wrapper nav a::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  content: ' ';
  background-color: transparent;
  -webkit-transition: all ease 0.4s;
  -o-transition: all ease 0.4s;
  transition: all ease 0.4s;
}
.nav-wrapper nav a.active::after {
  background-color: #A5C563;
}
.nav-wrapper nav a:hover {
  color: #bad388;
}
.nav-wrapper nav a:hover.active::after {
  background-color: #bad388;
}



