button.ink {
  border: 1px solid #6e6e6e;
  border-radius: 2rem;
  height: 1.6rem;
  padding: 0 1rem;
  font-size: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: #6e6e6e;
  background-color: white;
  cursor: pointer;
  outline: none;
}
button.ink:hover {
  border: 1px solid #A5C563;
  color: #A5C563;
}
button.ink:hover.primary {
  color: white;
  background-color: #bad388;
}
button.ink.primary {
  color: white;
  border-color: transparent;
  background-color: #A5C563;
}
button.ink .icon-loading {
  margin-left: 0.5rem;
  -webkit-animation: rotate 1s linear infinite;
          animation: rotate 1s linear infinite;
}
@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



