input.ink {
  max-width: 16rem;
  font-size: 1.3rem;
  height: 1.6rem;
  border-radius: 2rem;
  padding: 0rem 1rem;
  border: 2px solid #d0e1ad;
  outline: none;
}
input.ink.iconed {
  padding: 0rem 1rem 0rem 2rem;
}
input.ink:focus {
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}
input.ink[type="password"] {
  font-family: none;
}
i.ink.input {
  color: #d0e1ad;
  font-size: 1.3rem;
  position: absolute;
  left: 0.3rem;
  line-height: 2rem;
  cursor: pointer;
}
i.ink.input:hover {
  color: #bad388;
}
.ink.input-wrapper {
  position: relative;
}
textarea.ink {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  resize: vertical;
  font-size: 1.3rem;
  height: 100%;
  padding: 0.5rem;
  border-radius: 10px;
  border: 2px solid #d0e1ad;
  outline: none;
}
textarea.ink:focus {
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}



